/* Cookie consent start */
.wrapper {
	min-height: 25vh;
	background-color: #4070f4;
	position: fixed;
	bottom: 20px; /* Adjust this value to change the distance from the bottom */
	right: 20px; /* Adjust this value to change the distance from the right */
	max-width: 345px;
	width: 100%;
	background: #fff;
	border-radius: 8px;
	padding: 15px 25px 22px;
	transition: right 0.3s ease;
	box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
	z-index: 999;
  }
  
.wrapper.show {
  right: 20px;
}
.wrapper header {
  display: flex;
  align-items: center;
  column-gap: 15px;
}
.wrapper header i {
  color: #4070f4;
  font-size: 32px;
}
.wrapper header h2 {
  color: #4070f4;
  font-weight: 500;
}
